'use client'
import React, { useState } from 'react'
import Image from 'next/image'

const About = () => {
  const [hoveredSkill, setHoveredSkill] = useState<number | null>(null);
  const [hoveredTool, setHoveredTool] = useState<number | null>(null);

  // Development skills with their logos
  const devSkills = [
    { name: 'HTML5', logo: '/skills/html5.svg' },
    { name: 'CSS3', logo: '/skills/css3.svg' },
    { name: 'JavaScript', logo: '/skills/javascript.svg' },
    { name: 'React', logo: '/skills/react.svg' },
    { name: 'Next.js', logo: '/skills/nextjs.svg' },
    { name: 'Tailwind', logo: '/skills/tailwind.svg' },
    { name: 'Shadcn', logo: '/skills/shadcn.svg' },
    { name: 'Bootstrap', logo: '/skills/bootstrap.svg' },
    { name: 'MongoDB', logo: '/skills/mongodb.svg' },
    { name: 'Node.js', logo: '/skills/nodejs.svg' },
    { name: 'Express', logo: '/skills/express.svg' },
    { name: 'PHP', logo: '/skills/php.svg' },
    { name: 'MySQL', logo: '/skills/mysql.svg' },
    { name: 'Java', logo: '/skills/java.svg' },
    { name: 'C#', logo: '/skills/csharp.svg' },
    { name: 'VS Code', logo: '/skills/vscode.svg' },
  ]

  // Tools skills with their logos
  const toolsSkills = [
    { name: 'Figma', logo: '/skills/figma.svg' },
    { name: 'Canva', logo: '/skills/canva.svg' },
    { name: 'MySQL', logo: '/skills/mysql.svg' },
    { name: 'GitHub', logo: '/skills/github.svg' },
    { name: 'Unity', logo: '/skills/unity.svg' },
  ]

  return (
    <>
      {/* About Section */}
      <section id="about" className="py-20 bg-[#34353A] relative overflow-hidden">
        {/* Background gradient orbs */}
        <div className="absolute w-80 h-80 rounded-full bg-[#FAD26E]/20 blur-3xl right-20 -top-40"></div>
        <div className="absolute w-80 h-80 rounded-full bg-[#FAD26E]/20 blur-3xl left-40 top-60"></div>
        
        <div className="container mx-auto px-4">
          <h2 className="text-5xl font-bold text-white mb-16">
            About Me<span className="text-[#FAD26E]">.</span>
          </h2>
          
          <div className="max-w-4xl">
            <p className="text-xl text-white leading-relaxed">
              I'm <span className="text-[#FAD26E]">Justin Mark Agustin</span>, a self-taught programmer from San Jose Del Monte. I love watching 
              movies, playing mobile and PC games, and <span className="text-[#FAD26E]">diving into self-study</span>. I'm passionate about design and 
              development, <span className="text-[#FAD26E]">focusing on creating exceptional user experiences</span>.
            </p>
          </div>
        </div>
      </section>

      {/* Skills Section */}
      <section id="skills" className="py-20 bg-[#34353A] relative overflow-hidden">
        {/* Background gradient orbs */}
        <div className="absolute w-80 h-80 rounded-full bg-[#FAD26E]/20 blur-3xl left-20 top-40"></div>
        <div className="absolute w-80 h-80 rounded-full bg-[#FAD26E]/20 blur-3xl right-40 bottom-20"></div>
        
        <div className="container mx-auto px-4">
          <h2 className="text-5xl font-bold text-white mb-16">
            My Skills<span className="text-[#FAD26E]">.</span>
          </h2>
          
          <div className="mb-16">
            <h3 className="text-2xl font-medium text-white mb-8">Development</h3>
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-8 gap-4">
              {devSkills.map((skill, index) => (
                <div 
                  key={index} 
                  className={`border border-zinc-700 rounded-lg p-4 flex flex-col items-center justify-center transition-all duration-300 ${
                    hoveredSkill === index ? 'bg-[#FAD26E]' : 'bg-[#34353A]/50'
                  }`}
                  onMouseEnter={() => setHoveredSkill(index)}
                  onMouseLeave={() => setHoveredSkill(null)}
                >
                  <div className="relative w-12 h-12 mb-2">
                    <div className="w-12 h-12 flex items-center justify-center text-4xl">
                      {/* If you have actual logos, uncomment this: */}
                      {/* <Image src={skill.logo} alt={skill.name} width={48} height={48} /> */}
                      
                      {/* Placeholder for now */}
                      <div className={`w-12 h-12 rounded-md flex items-center justify-center text-sm ${
                        hoveredSkill === index ? 'bg-[#34353A] text-white' : 'bg-zinc-700 text-white'
                      }`}>
                        {skill.name.substring(0, 2)}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
          
          <div>
            <h3 className="text-2xl font-medium text-white mb-8">Tools</h3>
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
              {toolsSkills.map((skill, index) => (
                <div 
                  key={index} 
                  className={`border border-zinc-700 rounded-lg p-4 flex flex-col items-center justify-center transition-all duration-300 ${
                    hoveredTool === index ? 'bg-[#FAD26E]' : 'bg-[#34353A]/50'
                  }`}
                  onMouseEnter={() => setHoveredTool(index)}
                  onMouseLeave={() => setHoveredTool(null)}
                >
                  <div className="relative w-12 h-12 mb-2">
                    <div className="w-12 h-12 flex items-center justify-center text-4xl">
                      {/* If you have actual logos, uncomment this: */}
                      {/* <Image src={skill.logo} alt={skill.name} width={48} height={48} /> */}
                      
                      {/* Placeholder for now */}
                      <div className={`w-12 h-12 rounded-md flex items-center justify-center text-sm ${
                        hoveredTool === index ? 'bg-[#34353A] text-white' : 'bg-zinc-700 text-white'
                      }`}>
                        {skill.name.substring(0, 2)}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default About